.page-project .project-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
}

.page-project .project-item h3{
  font-size: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  margin: 8.88px 0;
}

.page-project .project-cta {
  display: flex;
}

.page-project .project-cta a{
  padding: 8.88px 21.11px;
  display: block;
  color: inherit;
  text-decoration: none;
  font-size: 16px;
}

.page-project .project-cta a.view{
  background: var(--main-blue);
  color: #FFF;
}
.page-project .project-cta a.view:hover{
  background: var(--hover-main-blue);
} 
.page-project .project-cta a.source-code:hover{
  text-decoration: underline;
}

/* EricMartinQLZ */
.page-project .project-item img {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
  border-radius: 8.88px;
  width: 100%;
}


/* Responsive */
@media screen and (min-width: 560px) {
  .page-project .project-list {
    gap: 20px;
  }

  .page-project .project-item {
    width: calc(50% - 10px);
  }

  .page-project .project-cta a{
    padding: 6.66px 14.44px;
  }
}

@media screen and (min-width: 768px) {
  .page-project .project-list {
    justify-content: unset;
  }

  .page-project .project-item {
    width: calc(33% - 15px);
  }
}

@media screen and (min-width: 900px){
  .page-project .project-item {
    width: calc(25% - 15px);
  }
}

@media screen and (min-width: 768px) and (min-height:537px) {
  .page-project .project-item {
    width: 175px;
  }

  .page-project .project-cta a{
    font-size: 14px;
  }

  .page-project .project-item h3{
    font-size: 18px;
    min-height: 50px;
  }
}

@media screen and (min-width: 900px) and (min-height:537px) {
  .page-project .project-list {
    gap: 40px;
  }

  .page-project .project-item {
    width: 200px;
  }
}